import { QueryClient } from 'react-query';
import { isReview, Review } from '../api/data/Review';
import { User } from '../api/data/User';
import { QueryKey } from '../queryClient';
import { isArrayOf } from './isArrayOf';

export enum CacheEvent {
    ADD = 'add',
    UPDATE = 'update',
    DELETE = 'delete',
}

export const updateMeCache = (queryClient: QueryClient, review: Review | null, reviewId: number | null, event: CacheEvent) => {

    const me: User | undefined = queryClient.getQueryData(QueryKey.ME);

    if (me) {
        if (event === CacheEvent.DELETE) {
            queryClient.setQueryData(QueryKey.ME, {
                ...me,
                reviews: me.reviews.filter(r => r.id !== reviewId),
            });
        }

        if(event === CacheEvent.UPDATE && review) {
            queryClient.setQueryData(QueryKey.ME, {
                ...me,
                reviews: me.reviews.map(r => (r.id === reviewId) ? review : r),
            });
        }

        if (event === CacheEvent.ADD && review) {
            queryClient.setQueryData(QueryKey.ME, {
                ...me,
                reviews: [...me.reviews, review],
            });
        }
    }

}

export const updateReviewsCache = (queryClient: QueryClient, review: Review | null, reviewId: number | null, event: CacheEvent) => {

    let reviews = queryClient.getQueryData(QueryKey.REVIEWS);

    if ([CacheEvent.UPDATE, CacheEvent.DELETE].includes(event)) {
        if (isArrayOf(reviews, isReview)) {
            if (event === CacheEvent.UPDATE) {
                reviews.forEach(r => (r.id === reviewId) ? review : r);
            } else {
                reviews = reviews.filter(r => r.id !== reviewId);
            }
        }
    }

    if (event === CacheEvent.ADD) {
        if (!reviews) {
            reviews = [review];
        } else {
            if (isArrayOf(reviews, isReview) && review) {
                reviews.push(review);
            }
        }
    }

    queryClient.setQueryData(QueryKey.REVIEWS, reviews);
}